.wrap {
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
}

.wrap .content {
    display: flex;
    width: 100%;
    height: 100%;
}

.wrap .content .sider {
    display: flex;
    width: 12%;
    position: relative;
    height: 100%;
    background-color: rgba(127, 255, 165, 0.337);
    border-width: 0px;
    border-right-width: 1px;
    border-style: solid;
    border-left-color: black;
}

.wrap .content .panel {
    display: flex;
    height: 100%;
    width: 72%;
    flex-direction: column;
}

.wrap .content .panel .toolbar {
    width: 100%;
    height: 40px;
    border-width: 0px;
    border-bottom-width: 1px;
    border-style: solid;
    border-bottom-color: black;
}

.wrap .content .config {
    padding: 0;
    display: flex;
    width: 16%;
    height: 100%;
    border-width: 0px;
    border-left-width: 1px;
    border-style: solid;
    border-left-color: black;
    background-color: rgba(255, 255, 255, 0.952);
}

#container {
    height: 100%;
    width: 100%;
}

.x6-widget-stencil-title,
.x6-widget-stencil-group>.x6-widget-stencil-group-title {
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    align-content: center;
    border-width: 0px;
    border-bottom-width: 1px;
    border-style: solid;
    border-bottom-color: black;
}

.ant-tabs>.ant-tabs-nav,
.ant-tabs>div>.ant-tabs-nav {
    padding: 0 14px;
    border-width: 0px;
    border-bottom-width: 1px;
    border-style: solid;
    border-bottom-color: black;
}

.configPanel .ant-tabs-top>.ant-tabs-nav,
.ant-tabs-bottom>.ant-tabs-nav,
.ant-tabs-top>div>.ant-tabs-nav,
.ant-tabs-bottom>div>.ant-tabs-nav {
    margin-bottom: 5px;
    height: 40px;
}

.configPanel .ant-tabs-content-holder {
    padding: 0 18px;
}